<template>
  <div class="slider-block">
    <div class="slider">
      <div class="slides">
        <input
          @click="
            {
              {
                counter = 1;
              }
            }
          "
          type="radio"
          name="radio-btn"
          id="radio1"
        />
        <input
          @click="
            {
              {
                counter = 2;
              }
            }
          "
          type="radio"
          name="radio-btn"
          id="radio2"
        />
        <input
          @click="
            {
              {
                counter = 3;
              }
            }
          "
          type="radio"
          name="radio-btn"
          id="radio3"
        />
        <input
          @click="
            {
              {
                counter = 4;
              }
            }
          "
          type="radio"
          name="radio-btn"
          id="radio4"
        />

        <div class="slide first">
          <div class="slide-image">
            <img src="/slides/slide1.jpg" alt="nothing" />
            <div class="slide-text">
              <h3>Ремонт и обслуживание медицинского оборудования</h3>
            </div>
          </div>
        </div>

        <div class="slide">
          <div class="slide-image">
            <img src="/slides/slide2.jpg" alt="nothing" />
            <div class="slide-text">
              <h3>Более 15 лет на рынке</h3>
            </div>
          </div>
        </div>

        <div class="slide">
          <div class="slide-image">
            <img src="/slides/slide3.jpg" alt="nothing" />
            <div class="slide-text">
              <h3>Гарантия качества услуг</h3>
            </div>
          </div>
        </div>

        <div class="slide">
          <img class="slide-image" src="/slides/slide4.jpg" alt="nothing" />
        </div>

        <div class="navigation-auto">
          <div class="auto-btn1"></div>
          <div class="auto-btn2"></div>
          <div class="auto-btn3"></div>
          <div class="auto-btn4"></div>
        </div>
      </div>

      <div class="navigation-manual">
        <label for="radio1" class="manual-btn"></label>
        <label for="radio2" class="manual-btn"></label>
        <label for="radio3" class="manual-btn"></label>
        <label for="radio4" class="manual-btn"></label>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      counter: 1,
    };
  },
  methods: {
    sliderGo() {
      setInterval(() => {
        let slide = this.getSlide();
        if (slide) {
          slide.checked = true;
          this.counter++;
        }
        if (this.counter > 4) {
          this.counter = 1;
        }
      }, 5000);
    },
    getSlide() {
      return document.getElementById("radio" + this.counter);
    },
  },
  mounted: function () {
    this.sliderGo();
  },
};
</script>

<style>
.slider-block {
  position: absolute;
  z-index: 1;
  height: auto;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider {
  width: 100%;
  height: 88.5vh;
  overflow: hidden;
}

.slides {
  width: 500%;
  height: 88.5vh;
  display: flex;
}

.slides input {
  display: none;
}

.slide {
  width: 20%;
  transition: 2s;
}

.slide-image {
  width: 100%;
  height: 88.5vh;
  position: relative;
}

.slide-image img {
  width: 100%;
  height: 100%;
}

.slide-text {
  position: absolute;
  display: flex;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h3 {
  text-shadow: 1px 1px 2px black;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 3rem;
  text-align: center;
  font-weight: bold;
  color: aliceblue;
}

.navigation-manual {
  position: absolute;
  width: 100%;
  margin-top: -8.5vh;
  display: flex;
  justify-content: center;
}

.manual-btn {
  border: 2px solid rgb(185, 255, 255);
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
  transition: 1s;
}

.manual-btn:not(:last-child) {
  margin-right: 40px;
}

.manual-btn:hover {
  background-color: rgb(185, 255, 255);
}

#radio1:checked ~ .first {
  margin-left: 0;
}

#radio2:checked ~ .first {
  margin-left: -20%;
}

#radio3:checked ~ .first {
  margin-left: -40%;
}

#radio4:checked ~ .first {
  margin-left: -60%;
}

.navigation-auto {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 80vh;
}

.navigation-auto div {
  border: 2px solid rgb(185, 255, 255);
  padding: 5px;
  border-radius: 10px;
  transition: 1s;
}

.navigation-auto div:not(:last-child) {
  margin-right: 40px;
}

#radio1:checked ~ .navigation-auto .auto-btn1 {
  background-color: rgb(185, 255, 255);
}

#radio2:checked ~ .navigation-auto .auto-btn2 {
  background-color: rgb(185, 255, 255);
}

#radio3:checked ~ .navigation-auto .auto-btn3 {
  background-color: rgb(185, 255, 255);
}

#radio4:checked ~ .navigation-auto .auto-btn4 {
  background-color: rgb(185, 255, 255);
}
</style>
