<template>
  <div class="navbar">
    <div class="titel"><h1>Hermes</h1></div>
    <ul class="buttons" v-bind:class="[open ? activ : passiv]">
      <li v-for="link in links">
        <a :href="link.link">{{ link.name }}</a>
      </li>
    </ul>
    <img
      v-if="show === true"
      @click="changeMenu"
      class="menuOpen menuShow"
      src="../assets/menuOpen.png"
    />
    <img
      v-else
      @click="changeMenu"
      class="menuClose menuShow"
      src="../assets/menuClose.png"
    />
  </div>
  <div class="placeholder"><h1>#</h1></div>
</template>

<script>
export default {
  setup() {
    let links = [
      { name: "Главная", link: "#/main" },
      { name: "Услуги", link: "#/services" },
      { name: "Контакты", link: "#/contacts" },
      { name: "О нас", link: "#/about" },
    ];
    return { links };
  },
  data() {
    return { show: true, open: false, activ: "activ", passiv: "" };
  },
  methods: {
    changeMenu() {
      this.open = !this.open;
      this.show = !this.show;
    },
  },
};
</script>

<style>
.navbar {
  position: fixed;
  z-index: 5;
  top: 0;
  box-sizing: border-box;
  width: 100%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: rgb(212, 212, 212);
  box-shadow: 4px 4px 14px rgba(0, 0, 0, 0.25);
  background-color: rgb(90, 90, 90);
  padding: 0.75rem 1.5rem;
  justify-content: space-between;
  align-items: center;
}

.buttons {
  z-index: 100;
}

.placeholder {
  background-color: black;
  padding: 0.75rem 1.5rem;
}

.titel {
  display: flex;
  align-items: center;
}

h1 {
  font-weight: bold;
  color: rgb(185, 255, 255);
  font-size: 1.5rem;
  line-height: 1.75rem;
}

ul {
  padding-left: 0;
}

li {
  font-size: 1rem;
}
.menuOpen {
  cursor: pointer;
  width: 30px;
  height: 30px;
  position: fixed;
  right: 1.75rem;
  top: 1.75rem;
}

.menuClose {
  cursor: pointer;
  width: 20px;
  height: 20px;
  position: fixed;
  right: 1.75rem;
  top: 2.1rem;
}

a:hover {
  color: rgb(185, 255, 255);
}

@media screen and (min-width: 769px) {
  .menuShow {
    display: none;
  }
  .navbar {
    display: flex;
  }
  .buttons {
    display: flex;
    align-items: center;
  }
  li {
    margin-left: 1rem;
    margin-right: 1rem;
  }
}

@media screen and (max-width: 768px) {
  .buttons {
    height: 100%;
    width: 130px;
    transform: translateX(-150%);
    transition: 0.5s;
    position: fixed;
    margin-top: 5px;
    left: 0;
    background-color: rgb(90, 90, 90);
  }
  .activ {
    transform: translateX(0%);
  }
  li {
    padding: 0 10px 30px 10px;
    text-align: center;
  }
  li:first-child {
    padding-top: 20px;
  }
}
</style>
