<template>
  <div class="services">
    <div class="min-menu">
      <div class="links-min">
        <div
          v-for="(device, ind) in devices"
          v-on:click="scrollToDev(ind)"
          class="link-min"
        >
          <a>{{ device }}</a>
        </div>
      </div>
      <button @click="scrollToTop()" id="BtnToTop">К началу</button>
    </div>
    <div class="devices-container">
      <p>
        Предприятие "Гермес" занимается ремонтом и техническим обслуживанием
        нижеперечисленного оборудования
      </p>
      <hr />
      <div class="devices-list">
        <div
          v-for="(device, ind) in devices"
          v-bind:ref="'device' + ind"
          class="device-list"
        >
          <h5 :id="'device' + ind">{{ device }}</h5>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      devices: [
        "Автоклавы",
        "Аппараты физиотерапии",
        "Электрокардиографы",
        "Энцифоллографы",
        "Реогафы",
        "Манометры",
        "Тонометры",
        "Гигрометр",
        "Тягонапоромеры",
        "Термометры",
        "Автоклавы",
        "Аппараты физиотерапии",
        "Электрокардиографы",
        "Энцифоллографы",
        "Реогафы",
        "Манометры",
        "Тонометры",
        "Гигрометр",
        "Тягонапоромеры",
        "Термометры",
      ],
    };
  },
  created() {
    window.addEventListener("scroll", this.scrollFunction);
  },
  methods: {
    scrollFunction() {
      if (document.getElementById("BtnToTop")) {
        if (
          document.body.scrollTop > 20 ||
          document.documentElement.scrollTop > 20
        ) {
          document.getElementById("BtnToTop").style.display = "block";
        } else {
          document.getElementById("BtnToTop").style.display = "none";
        }
      }
    },
    scrollToTop() {
      window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
    },
    scrollToDev(ind) {
      document
        .getElementById("device" + ind)
        .scrollIntoView({ behavior: "smooth", block: "center" });
    },
  },
};
</script>

<style>
body {
  height: 100%;
}

.link-min a {
  font-size: 20px;
  color: rgb(126, 126, 126);
}

.link-min a:hover {
  cursor: pointer;
  text-decoration: underline;
}

.min-menu {
  position: fixed;
  right: 30px;
  top: 20%;
  width: 20%;
  height: 70%;
  background-color: rgb(238, 255, 253);
}

.links-min {
  hyphens: auto;
  max-width: 100%;
  height: 100%;
  text-align: center;
  overflow-y: scroll;
}

.link-min {
  border: 1px solid rgb(117, 117, 117);
  padding-top: 5px;
  padding-bottom: 5px;
}

#BtnToTop {
  display: none;
  position: fixed;
  width: 20%;
  bottom: 12px;
  right: 30px;
  z-index: 99;
  border: none;
  outline: none;
  color: rgb(90, 90, 90);
  cursor: pointer;
  padding: 15px;
  border-radius: 10px;
  font-size: 18px;
}

#myBtn:hover {
  background-color: #555;
}

.device-list {
  padding: 20px 0 150px 0;
}

.devices-container {
  box-sizing: border-box;
  border-radius: 10px;
  margin: 20px 0 0 40px;
  padding: 10px;
  padding-left: 30px;
  padding-right: 30px;
  width: 70%;
  background-color: rgb(238, 255, 253);
}

p {
  font-size: 25px;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .services {
    display: flex;
    justify-content: center;
    padding-top: 20px;
  }
  .min-menu {
    display: none;
  }

  .devices-container {
    padding: 0;
    margin: 0;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  h5 {
    text-align: center;
  }
}
</style>
